/* CDN 기반 폰트 시스템 - 안정적이고 빠른 로딩 */

/* 글로벌 폰트 적용 */
body, * {
  font-family: 'Noto Sans KR', 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Material-UI 컴포넌트 폰트 오버라이드 */
.MuiTypography-root,
.MuiButton-root,
.MuiInputBase-root,
.MuiFormLabel-root,
.MuiMenuItem-root,
.MuiChip-root,
.MuiTableCell-root,
.MuiAlert-root,
.MuiDialogTitle-root,
.MuiDialogContent-root,
.MuiListItemText-root {
  font-family: 'Noto Sans KR', 'Inter', sans-serif !important;
}

/* 폰트 무게 클래스 */
.font-light {
  font-weight: 300 !important;
}

.font-normal {
  font-weight: 400 !important;
}

.font-medium {
  font-weight: 500 !important;
}

.font-semibold {
  font-weight: 600 !important;
}

.font-bold {
  font-weight: 700 !important;
}

/* 숫자 표시용 고정폭 폰트 */
.numeric-display {
  font-family: 'Inter', 'Roboto Mono', monospace !important;
  font-variant-numeric: tabular-nums;
  font-weight: 500;
}

/* 한글 타이포그래피 개선 */
.MuiTypography-h1,
.MuiTypography-h2,
.MuiTypography-h3 {
  letter-spacing: -0.03em;
  font-weight: 700;
}

.MuiTypography-h4,
.MuiTypography-h5,
.MuiTypography-h6 {
  letter-spacing: -0.02em;
  font-weight: 600;
}

.MuiTypography-body1,
.MuiTypography-body2 {
  letter-spacing: -0.01em;
  line-height: 1.7;
  font-weight: 400;
}

/* 버튼 폰트 스타일 */
.MuiButton-root {
  letter-spacing: 0;
  font-weight: 500;
}

/* 입력 필드 폰트 스타일 */
.MuiInputBase-root {
  letter-spacing: -0.01em;
  font-weight: 400;
}

/* 테이블 셀 폰트 스타일 */
.MuiTableCell-root {
  letter-spacing: -0.01em;
  font-weight: 400;
}

/* 브랜딩 폰트 */
.brand-font {
  font-family: 'Inter', sans-serif !important;
  font-weight: 700;
}

/* 로딩 및 오류 메시지 */
.loading-text {
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  color: #FF5722;
}

.error-text {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  color: #d32f2f;
}
